import React, { useEffect, useState } from 'react';
import {
  Box,
  Typography,
  Card,
  CardHeader,
  CardContent,
  CircularProgress,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Paper,
} from '@mui/material';
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';
import { getAllOrders } from '../../../api/orders'; // import the new function
import Loader from '../../../components/Loader/Loader';

const HistorySection = () => {
  const [orderHistory, setOrderHistory] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    const getData = async () => {
      try {
        const historyData = await getAllOrders(); // ÿßÿ≥ÿ™ŸÅÿßÿØŸá ÿßÿ≤ ÿ™ÿßÿ®ÿπ ÿ¨ÿØ€åÿØ
        setOrderHistory(historyData);
      } catch (error) {
        console.error("ÿÆÿ∑ÿß ÿØÿ± ÿØÿ±€åÿßŸÅÿ™ ÿ™ÿßÿ±€åÿÆ⁄ÜŸá ÿ≥ŸÅÿßÿ±ÿ¥ÿßÿ™:", error);
        setError("ÿÆÿ∑ÿß ÿØÿ± ÿØÿ±€åÿßŸÅÿ™ ÿ™ÿßÿ±€åÿÆ⁄ÜŸá ÿ≥ŸÅÿßÿ±ÿ¥ÿßÿ™.");
      } finally {
        setLoading(false);
      }
    };
    getData();
  }, []);

  if (loading) {
    return (
<Loader/>
    );
  }

  if (error) {
    return (
      <Typography variant="body1" sx={{ color: 'red', textAlign: 'center' }}>
        {error}
      </Typography>
    );
  }

  return (
    <Card sx={{ background:"transparent", borderRadius: '8px' }}>
      <CardHeader
        title={
          <Typography variant="h6" sx={{ color: '#ffa05c', fontWeight: 'bold' }}>
            üìú ÿ™ÿßÿ±€åÿÆ⁄ÜŸá ÿ≥ŸÅÿßÿ±ÿ¥ÿßÿ™ (ÿßÿØŸÖ€åŸÜ)
          </Typography>
        }
      />
      <CardContent>
        <List>
          {orderHistory.map((order, index) => (
            <Paper
              key={index}
              sx={{
                backgroundColor: '#ffe5cc',
                borderRadius: '8px',
                padding: '16px',
                marginBottom: '16px',
              }}
            >
              <ListItem>
                <ListItemIcon>
                  <EmojiEventsIcon sx={{ color: '#ffa05c' }} />
                </ListItemIcon>
                <ListItemText
                  primary={
                    <Typography variant="body1" sx={{ color: '#ffa05c' }}>
                      ⁄©ÿßÿ±ÿ®ÿ±: {order.user.username}
                    </Typography>
                  }
                  secondary={
                    <>
                      <Typography variant="body2" sx={{ color: '#ffa05c' }}>
                        Ÿàÿ∂ÿπ€åÿ™: {order.status === 'completed' ? 'ÿßÿ±ÿ≥ÿßŸÑ ÿ¥ÿØŸá' : 'ŸÖÿ±ÿ¨Ÿàÿπ ÿ¥ÿØŸá'}
                      </Typography>
                      <Typography variant="body2" sx={{ color: '#ffa05c' }}>
                        ÿ™ÿßÿ±€åÿÆ: {new Date(order.createdAt).toLocaleDateString()}
                      </Typography>
                    </>
                  }
                />
              </ListItem>
              <List>
                {order.products.map((item, idx) => (
                  <ListItem key={idx}>
                    <ListItemText
                      primary={
                        <Typography variant="body2" sx={{ color: '#ffa05c' }}>
                          {item.product.name} - ÿ™ÿπÿØÿßÿØ: {item.quantity}
                        </Typography>
                      }
                    />
                  </ListItem>
                ))}
              </List>
            </Paper>
          ))}
        </List>
      </CardContent>
    </Card>
  );
};

export default HistorySection;