import React, { useState } from 'react';
import {
  Card,
  CardHeader,
  CardContent,
  Typography,
  TextField,
  Button,
  InputAdornment,
  CircularProgress,
  Box,
} from '@mui/material';
import PersonIcon from '@mui/icons-material/Person'; // ÿ¢€å⁄©ŸàŸÜ ŸÜÿßŸÖ
import EmailIcon from '@mui/icons-material/Email'; // ÿ¢€å⁄©ŸàŸÜ ÿß€åŸÖ€åŸÑ
import PhotoCameraIcon from '@mui/icons-material/PhotoCamera'; // ÿ¢€å⁄©ŸàŸÜ ÿ™ÿµŸà€åÿ± Ÿæÿ±ŸàŸÅÿß€åŸÑ

const ProfileSettings = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    // ÿ¥ÿ®€åŸá‚Äåÿ≥ÿßÿ≤€å ÿπŸÖŸÑ€åÿßÿ™ ÿ∞ÿÆ€åÿ±Ÿá ÿ™ŸÜÿ∏€åŸÖÿßÿ™
    setTimeout(() => {
      setLoading(false);
      alert('ÿ™ŸÜÿ∏€åŸÖÿßÿ™ Ÿæÿ±ŸàŸÅÿß€åŸÑ ÿ®ÿß ŸÖŸàŸÅŸÇ€åÿ™ ÿ∞ÿÆ€åÿ±Ÿá ÿ¥ÿØ!');
    }, 2000);
  };

  return (
    <Card sx={{ borderRadius: '8px' }}>
      <CardHeader
        title={
          <Typography variant="h6" sx={{ color: '#ffa05c', fontWeight: 'bold' }}>
            üë§ ÿ™ŸÜÿ∏€åŸÖÿßÿ™ Ÿæÿ±ŸàŸÅÿß€åŸÑ
          </Typography>
        }
        action={
          <Button
            variant="contained"
            sx={{
              backgroundColor: '#ffa05c',
              '&:hover': { backgroundColor: '#e68a4a' }, // ÿ™ÿ∫€å€åÿ± ÿ±ŸÜ⁄Ø ŸáŸÜ⁄ØÿßŸÖ ŸáÿßŸàÿ±
              color: '#fff',
              fontWeight: 'bold',
            }}
            onClick={handleSubmit}
            disabled={loading}
          >
            {loading ? <CircularProgress size={24} sx={{ color: '#fff' }} /> : 'ÿ∞ÿÆ€åÿ±Ÿá ÿ™ŸÜÿ∏€åŸÖÿßÿ™ Ÿæÿ±ŸàŸÅÿß€åŸÑ'}
          </Button>
        }
      />
      <CardContent>
        <form onSubmit={handleSubmit}>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
            {/* ŸÅ€åŸÑÿØ ŸÜÿßŸÖ */}
            <TextField
              id="profile-name"
              label="ŸÜÿßŸÖ"
              placeholder="ŸÜÿßŸÖ ⁄©ÿßÿ±ÿ®ÿ±€å"
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <PersonIcon sx={{ color: '#ffa05c' }} /> {/* ÿ¢€å⁄©ŸàŸÜ ŸÜÿßŸÖ */}
                  </InputAdornment>
                ),
              }}
              fullWidth
              sx={{
                '& .MuiOutlinedInput-root': {
                  '& fieldset': { borderColor: '#ffa05c' },
                  '&:hover fieldset': { borderColor: '#e68a4a' },
                },
              }}
            />

            {/* ŸÅ€åŸÑÿØ ÿß€åŸÖ€åŸÑ */}
            <TextField
              id="profile-email"
              label="ÿß€åŸÖ€åŸÑ"
              placeholder="ÿß€åŸÖ€åŸÑ"
              type="email"
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <EmailIcon sx={{ color: '#ffa05c' }} /> {/* ÿ¢€å⁄©ŸàŸÜ ÿß€åŸÖ€åŸÑ */}
                  </InputAdornment>
                ),
              }}
              fullWidth
              sx={{
                '& .MuiOutlinedInput-root': {
                  '& fieldset': { borderColor: '#ffa05c' },
                  '&:hover fieldset': { borderColor: '#e68a4a' },
                },
              }}
            />

            {/* ŸÅ€åŸÑÿØ ÿ™ÿµŸà€åÿ± Ÿæÿ±ŸàŸÅÿß€åŸÑ */}
            <TextField
              id="profile-picture"
              label="ÿ™ÿµŸà€åÿ± Ÿæÿ±ŸàŸÅÿß€åŸÑ"
              type="file"
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <PhotoCameraIcon sx={{ color: '#ffa05c' }} /> {/* ÿ¢€å⁄©ŸàŸÜ ÿ™ÿµŸà€åÿ± Ÿæÿ±ŸàŸÅÿß€åŸÑ */}
                  </InputAdornment>
                ),
              }}
              fullWidth
              sx={{
                '& .MuiOutlinedInput-root': {
                  '& fieldset': { borderColor: '#ffa05c' },
                  '&:hover fieldset': { borderColor: '#e68a4a' },
                },
              }}
            />
          </Box>
        </form>
        {error && (
          <Typography variant="body2" sx={{ color: 'red', textAlign: 'center', marginTop: '16px' }}>
            {error}
          </Typography>
        )}
      </CardContent>
    </Card>
  );
};

export default ProfileSettings;