import React, { useEffect, useState } from 'react';
import {
  Box,
  Typography,
  Avatar,
  CircularProgress,
  Grid,
  Paper,
} from '@mui/material';
import { fetchUserData } from '../../../api/auth'; // import the fetchUserInfo function
import Loader from '../../../components/Loader/Loader';

const UserInfoComponent = () => {
  const [userInfo, setUserInfo] = useState({
    name: "",
    bio: "",
    uploads: 0,
    achievements: 0,
    missions: 0,
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    const getUserInfo = async () => {
      try {
        const data = await fetchUserData(); // ÿßÿ≥ÿ™ŸÅÿßÿØŸá ÿßÿ≤ ÿ™ÿßÿ®ÿπ fetchUserInfo ÿßÿ≤ ŸÅÿß€åŸÑ api
        setUserInfo(data);
      } catch (error) {
        console.error("ÿÆÿ∑ÿß ÿØÿ± ÿØÿ±€åÿßŸÅÿ™ ÿßÿ∑ŸÑÿßÿπÿßÿ™ ⁄©ÿßÿ±ÿ®ÿ±:", error);
        setError("ÿÆÿ∑ÿß ÿØÿ± ÿØÿ±€åÿßŸÅÿ™ ÿßÿ∑ŸÑÿßÿπÿßÿ™ ⁄©ÿßÿ±ÿ®ÿ±.");
      } finally {
        setLoading(false);
      }
    };
    getUserInfo();
  }, []);

  if (loading) {
    return (
      <Loader/>
    );
  }

  if (error) {
    return (
      <Typography variant="body1" sx={{ color: 'red', textAlign: 'center' }}>
        {error}
      </Typography>
    );
  }

  return (
    <Paper
      sx={{
        background:"transparent",
        borderRadius: '8px',

      }}
    >
      <Avatar
        sx={{
          width: 100,
          height: 100,
          margin: '0 auto 16px',
          backgroundColor: '#ffa05c',
        }}
      >
        {userInfo.name.charAt(0)}
      </Avatar>
      <Typography variant="h4" sx={{ color: '#ffa05c', fontWeight: 'bold' }}>
        {userInfo.name}
      </Typography>
      <Typography variant="body1" sx={{ color: '#ffa05c', marginBottom: '16px' }}>
        {userInfo.bio}
      </Typography>
      <Grid container spacing={3}>
        <Grid item xs={12} sm={4}>
          <Paper
            sx={{
              backgroundColor: '#ffe5cc',
              borderRadius: '8px',
              padding: '16px',
              textAlign: 'center',
            }}
          >
            <Typography variant="h6" sx={{ color: '#ffa05c' }}>
              ÿ¢ŸæŸÑŸàÿØ Ÿáÿß
            </Typography>
            <Typography variant="h4" sx={{ color: '#ffa05c', fontWeight: 'bold' }}>
              {userInfo.uploads}
            </Typography>
          </Paper>
        </Grid>
        <Grid item xs={12} sm={4}>
          <Paper
            sx={{
              backgroundColor: '#ffe5cc',
              borderRadius: '8px',
              padding: '16px',
              textAlign: 'center',
            }}
          >
            <Typography variant="h6" sx={{ color: '#ffa05c' }}>
              ÿØÿ≥ÿ™ÿßŸàÿ±ÿØ Ÿáÿß
            </Typography>
            <Typography variant="h4" sx={{ color: '#ffa05c', fontWeight: 'bold' }}>
              {userInfo.achievements}
            </Typography>
          </Paper>
        </Grid>
        <Grid item xs={12} sm={4}>
          <Paper
            sx={{
              backgroundColor: '#ffe5cc',
              borderRadius: '8px',
              padding: '16px',
              textAlign: 'center',
            }}
          >
            <Typography variant="h6" sx={{ color: '#ffa05c' }}>
              ŸÖÿßŸÖŸàÿ±€åÿ™ Ÿáÿß
            </Typography>
            <Typography variant="h4" sx={{ color: '#ffa05c', fontWeight: 'bold' }}>
              {userInfo.missions}
            </Typography>
          </Paper>
        </Grid>
      </Grid>
    </Paper>
  );
};

export default UserInfoComponent;