import React, { useState } from 'react';
import {
  Card,
  CardHeader,
  CardContent,
  Typography,
  TextField,
  Button,
  InputAdornment,
  CircularProgress,
  Box,
} from '@mui/material';
import LockIcon from '@mui/icons-material/Lock'; // ÿ¢€å⁄©ŸàŸÜ ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ŸÅÿπŸÑ€å
import VpnKeyIcon from '@mui/icons-material/VpnKey'; // ÿ¢€å⁄©ŸàŸÜ ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ÿ¨ÿØ€åÿØ
import LockResetIcon from '@mui/icons-material/LockReset'; // ÿ¢€å⁄©ŸàŸÜ ÿ™ÿß€å€åÿØ ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ±

const SecuritySettings = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    // ÿ¥ÿ®€åŸá‚Äåÿ≥ÿßÿ≤€å ÿπŸÖŸÑ€åÿßÿ™ ÿ®ÿ±Ÿàÿ≤ÿ±ÿ≥ÿßŸÜ€å ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ±
    setTimeout(() => {
      setLoading(false);
      alert('ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ÿ®ÿß ŸÖŸàŸÅŸÇ€åÿ™ ÿ®ÿ±Ÿàÿ≤ÿ±ÿ≥ÿßŸÜ€å ÿ¥ÿØ!');
    }, 2000);
  };

  return (
    <Card sx={{ borderRadius: '8px' }}>
      <CardHeader
        title={
          <Typography variant="h6" sx={{ color: '#ffa05c', fontWeight: 'bold' }}>
            üîí ÿ™ŸÜÿ∏€åŸÖÿßÿ™ ÿßŸÖŸÜ€åÿ™€å
          </Typography>
        }
        action={
          <Button
            variant="contained"
            sx={{
              backgroundColor: '#ffa05c',
              '&:hover': { backgroundColor: '#e68a4a' }, // ÿ™ÿ∫€å€åÿ± ÿ±ŸÜ⁄Ø ŸáŸÜ⁄ØÿßŸÖ ŸáÿßŸàÿ±
              color: '#fff',
              fontWeight: 'bold',
            }}
            onClick={handleSubmit}
            disabled={loading}
          >
            {loading ? <CircularProgress size={24} sx={{ color: '#fff' }} /> : 'ÿ®ÿ±Ÿàÿ≤ÿ±ÿ≥ÿßŸÜ€å ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ±'}
          </Button>
        }
      />
      <CardContent>
        <form onSubmit={handleSubmit}>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
            {/* ŸÅ€åŸÑÿØ ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ŸÅÿπŸÑ€å */}
            <TextField
              id="current-password"
              label="ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ŸÅÿπŸÑ€å"
              placeholder="ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ŸÅÿπŸÑ€å"
              type="password"
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <LockIcon sx={{ color: '#ffa05c' }} /> {/* ÿ¢€å⁄©ŸàŸÜ ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ŸÅÿπŸÑ€å */}
                  </InputAdornment>
                ),
              }}
              fullWidth
              sx={{
                '& .MuiOutlinedInput-root': {
                  '& fieldset': { borderColor: '#ffa05c' },
                  '&:hover fieldset': { borderColor: '#e68a4a' },
                },
              }}
            />

            {/* ŸÅ€åŸÑÿØ ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ÿ¨ÿØ€åÿØ */}
            <TextField
              id="new-password"
              label="ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ÿ¨ÿØ€åÿØ"
              placeholder="ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ÿ¨ÿØ€åÿØ"
              type="password"
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <VpnKeyIcon sx={{ color: '#ffa05c' }} /> {/* ÿ¢€å⁄©ŸàŸÜ ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ÿ¨ÿØ€åÿØ */}
                  </InputAdornment>
                ),
              }}
              fullWidth
              sx={{
                '& .MuiOutlinedInput-root': {
                  '& fieldset': { borderColor: '#ffa05c' },
                  '&:hover fieldset': { borderColor: '#e68a4a' },
                },
              }}
            />

            {/* ŸÅ€åŸÑÿØ ÿ™ÿß€å€åÿØ ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± */}
            <TextField
              id="confirm-password"
              label="ÿ™ÿß€å€åÿØ ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ±"
              placeholder="ÿ™⁄©ÿ±ÿßÿ± ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± ÿ¨ÿØ€åÿØ"
              type="password"
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <LockResetIcon sx={{ color: '#ffa05c' }} /> {/* ÿ¢€å⁄©ŸàŸÜ ÿ™ÿß€å€åÿØ ÿ±ŸÖÿ≤ ÿπÿ®Ÿàÿ± */}
                  </InputAdornment>
                ),
              }}
              fullWidth
              sx={{
                '& .MuiOutlinedInput-root': {
                  '& fieldset': { borderColor: '#ffa05c' },
                  '&:hover fieldset': { borderColor: '#e68a4a' },
                },
              }}
            />
          </Box>
        </form>
        {error && (
          <Typography variant="body2" sx={{ color: 'red', textAlign: 'center', marginTop: '16px' }}>
            {error}
          </Typography>
        )}
      </CardContent>
    </Card>
  );
};

export default SecuritySettings;