import React, { useState } from 'react';
import {
  Card,
  CardHeader,
  CardContent,
  Typography,
  Switch,
  Button,
  CircularProgress,
  Box,
  FormControlLabel,
} from '@mui/material';
import EmailIcon from '@mui/icons-material/Email'; // ÿ¢€å⁄©ŸàŸÜ ÿß€åŸÖ€åŸÑ
import SmsIcon from '@mui/icons-material/Sms'; // ÿ¢€å⁄©ŸàŸÜ Ÿæ€åÿßŸÖ⁄©
import NotificationsIcon from '@mui/icons-material/Notifications'; // ÿ¢€å⁄©ŸàŸÜ Push

const NotificationSettings = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [emailNotifications, setEmailNotifications] = useState(false);
  const [smsNotifications, setSmsNotifications] = useState(false);
  const [pushNotifications, setPushNotifications] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    // ÿ¥ÿ®€åŸá‚Äåÿ≥ÿßÿ≤€å ÿπŸÖŸÑ€åÿßÿ™ ÿ∞ÿÆ€åÿ±Ÿá ÿ™ŸÜÿ∏€åŸÖÿßÿ™
    setTimeout(() => {
      setLoading(false);
      alert('ÿ™ŸÜÿ∏€åŸÖÿßÿ™ ÿßÿπŸÑÿßŸÜ‚ÄåŸáÿß ÿ®ÿß ŸÖŸàŸÅŸÇ€åÿ™ ÿ∞ÿÆ€åÿ±Ÿá ÿ¥ÿØ!');
    }, 2000);
  };

  return (
    <Card sx={{ borderRadius: '8px'}}>
      <CardHeader
        title={
          <Typography variant="h6" sx={{ color: '#ffa05c', fontWeight: 'bold' }}>
            üîî ÿ™ŸÜÿ∏€åŸÖÿßÿ™ ÿßÿπŸÑÿßŸÜ‚ÄåŸáÿß
          </Typography>
        }
        action={
          <Button
            variant="contained"
            sx={{
              backgroundColor: '#ffa05c',
              '&:hover': { backgroundColor: '#e68a4a' }, // ÿ™ÿ∫€å€åÿ± ÿ±ŸÜ⁄Ø ŸáŸÜ⁄ØÿßŸÖ ŸáÿßŸàÿ±
              color: '#fff',
              fontWeight: 'bold',
            }}
            onClick={handleSubmit}
            disabled={loading}
          >
            {loading ? <CircularProgress size={24} sx={{ color: '#fff' }} /> : 'ÿ∞ÿÆ€åÿ±Ÿá ÿ™ŸÜÿ∏€åŸÖÿßÿ™ ÿßÿπŸÑÿßŸÜ'}
          </Button>
        }
      />
      <CardContent>
        <form onSubmit={handleSubmit}>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
            {/* ÿßÿπŸÑÿßŸÜ‚ÄåŸáÿß€å ÿß€åŸÖ€åŸÑ */}
            <FormControlLabel
              control={
                <Switch
                  checked={emailNotifications}
                  onChange={(e) => setEmailNotifications(e.target.checked)}
                  color="primary"
                />
              }
              label={
                <Box sx={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                  <EmailIcon sx={{ color: '#ffa05c' }} /> {/* ÿ¢€å⁄©ŸàŸÜ ÿß€åŸÖ€åŸÑ */}
                  <Typography variant="body1" sx={{ color: '#ffa05c' }}>
                    ÿßÿπŸÑÿßŸÜ‚ÄåŸáÿß€å ÿß€åŸÖ€åŸÑ
                  </Typography>
                </Box>
              }
            />

            {/* ÿßÿπŸÑÿßŸÜ‚ÄåŸáÿß€å Ÿæ€åÿßŸÖ⁄©€å */}
            <FormControlLabel
              control={
                <Switch
                  checked={smsNotifications}
                  onChange={(e) => setSmsNotifications(e.target.checked)}
                  color="primary"
                />
              }
              label={
                <Box sx={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                  <SmsIcon sx={{ color: '#ffa05c' }} /> {/* ÿ¢€å⁄©ŸàŸÜ Ÿæ€åÿßŸÖ⁄© */}
                  <Typography variant="body1" sx={{ color: '#ffa05c' }}>
                    ÿßÿπŸÑÿßŸÜ‚ÄåŸáÿß€å Ÿæ€åÿßŸÖ⁄©€å
                  </Typography>
                </Box>
              }
            />

            {/* ÿßÿπŸÑÿßŸÜ‚ÄåŸáÿß€å Push */}
            <FormControlLabel
              control={
                <Switch
                  checked={pushNotifications}
                  onChange={(e) => setPushNotifications(e.target.checked)}
                  color="primary"
                />
              }
              label={
                <Box sx={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                  <NotificationsIcon sx={{ color: '#ffa05c' }} /> {/* ÿ¢€å⁄©ŸàŸÜ Push */}
                  <Typography variant="body1" sx={{ color: '#ffa05c' }}>
                    ÿßÿπŸÑÿßŸÜ‚ÄåŸáÿß€å Push
                  </Typography>
                </Box>
              }
            />
          </Box>
        </form>
        {error && (
          <Typography variant="body2" sx={{ color: 'red', textAlign: 'center', marginTop: '16px' }}>
            {error}
          </Typography>
        )}
      </CardContent>
    </Card>
  );
};

export default NotificationSettings;