import React, { useEffect, useState } from 'react';
import {
  Box,
  Typography,
  Card,
  CardHeader,
  CardContent,
  CircularProgress,
  Grid,
  Paper,
} from '@mui/material';
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';
import { fetchProductsByAdmin } from '../../../api/products';
import Loader from '../../../components/Loader/Loader';

const AdminProductsSection = ({ userId }) => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    const getData = async () => {
      try {
        const data = await fetchProductsByAdmin(userId);
        setProducts(data);
      } catch (error) {
        console.error("ÿÆÿ∑ÿß ÿØÿ± ÿØÿ±€åÿßŸÅÿ™ ŸÖÿ≠ÿµŸàŸÑÿßÿ™ ÿßÿØŸÖ€åŸÜ:", error);
        setError("ÿÆÿ∑ÿß ÿØÿ± ÿØÿ±€åÿßŸÅÿ™ ŸÖÿ≠ÿµŸàŸÑÿßÿ™ ÿßÿØŸÖ€åŸÜ.");
      } finally {
        setLoading(false);
      }
    };
    getData();
  }, [userId]);

  if (loading) {
    return (
<Loader/>
    );
  }

  if (error) {
    return (
      <Typography variant="body1" sx={{ color: 'red', textAlign: 'center' }}>
        {error}
      </Typography>
    );
  }

  return (
    <Card sx={{ background:"transparent", borderRadius: '8px' }}>
      <CardHeader
        title={
          <Typography variant="h6" sx={{ color: '#ffa05c', fontWeight: 'bold' }}>
            üõí ŸÖÿ≠ÿµŸàŸÑÿßÿ™ ÿßÿØŸÖ€åŸÜ
          </Typography>
        }
      />
      <CardContent>
        <Grid container spacing={3}>
          {products.map((product, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Paper
                sx={{
                  backgroundColor: '#ffe5cc',
                  borderRadius: '8px',
                  padding: '16px',
                  textAlign: 'center',
                }}
              >
                <EmojiEventsIcon sx={{ color: '#ffa05c', fontSize: '40px' }} />
                <Box sx={{ marginTop: '8px' }}>
                  <Typography variant="body1" sx={{ color: '#ffa05c' }}>
                    ŸÜÿßŸÖ ŸÖÿ≠ÿµŸàŸÑ: {product.name}
                  </Typography>
                  <Typography variant="body1" sx={{ color: '#ffa05c' }}>
                    ŸÇ€åŸÖÿ™: {product.price}
                  </Typography>
                  <Typography variant="body1" sx={{ color: '#ffa05c' }}>
                    ŸÖŸàÿ¨ŸàÿØ€å: {product.stock}
                  </Typography>
                  <Typography variant="body1" sx={{ color: '#ffa05c' }}>
                    ÿØÿ≥ÿ™Ÿá‚Äåÿ®ŸÜÿØ€å: {product.category.name}
                  </Typography>
                  <Typography variant="body1" sx={{ color: '#ffa05c' }}>
                    ÿ≤€åÿ±ÿØÿ≥ÿ™Ÿá‚Äåÿ®ŸÜÿØ€å: {product.subCategory.name}
                  </Typography>
                </Box>
              </Paper>
            </Grid>
          ))}
        </Grid>
      </CardContent>
    </Card>
  );
};

export default AdminProductsSection;